
.footer-container {
    width: auto;
    min-width: 320px;

    display: flex;
    flex-direction: column;
    justify-content: center;

    gap: 3em;

    border-top: solid 1px #9F9F9F;
}

@media (max-width: 40em) {
    .footer-container {
        gap: 0;
    }
}

.footer-container .upper-section {
    margin-block: 2rem;

    width: 100%;

    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;
}

.upper-section .footer-navigation{
    margin-block: 2rem;

    display: flex;
    flex-direction: row;
    gap: 8em;

    list-style: none;
}

@media (max-width: 40em) {
    .upper-section .footer-navigation{
        gap: 4em;
    } 
}

@media (max-width: 40em) {
    .upper-section .footer-navigation{
        gap: 3em;
    } 
}

.footer-navigation .navigation-link {
    text-decoration: none;
    font-family:'Poppins', sans-serif;
    font-size: 1rem;
    font-weight: 500;
    color: #000000;
}

.footer-container .lower-section {
    padding-inline: 3em;
    width: 100%;

    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: space-between;
}

@media (max-width: 40em) {
    .footer-container .lower-section {
        padding-inline: 2rem;
    } 
}

.lower-section .logo-section {
    display: flex;
    flex-direction: row;

    align-items: center;
}

.logo-section img {
    width: min(10vw, 5rem);
    height: auto;
    margin: 0.5rem;
}